function hasShadowRoot(element: Element): boolean {
  // First check for open shadow root
  if (element.shadowRoot) {
    return true;
  }

  return false;
}

function getAllIframesInRootNode(
  root: Document | ShadowRoot,
  checkShadowRoot: boolean = false
): HTMLIFrameElement[] {
  const iframes: HTMLIFrameElement[] = [];

  // Get direct iframes in current root
  root
    .querySelectorAll(
      'iframe[src^="chrome-extension://"], [data-lastpass-root]'
    )
    .forEach((element) => iframes.push(element as HTMLIFrameElement));

  if (!checkShadowRoot) {
    return iframes;
  }

  // Create a TreeWalker to efficiently traverse the DOM
  const walker = document.createTreeWalker(
    root instanceof ShadowRoot ? root : root.documentElement,
    NodeFilter.SHOW_ELEMENT,
    {
      acceptNode: (node: Node) => {
        return hasShadowRoot(node as Element)
          ? NodeFilter.FILTER_ACCEPT
          : NodeFilter.FILTER_SKIP;
      },
    }
  );

  // Walk through all nodes that have shadow roots
  let currentNode: Node | null;
  while ((currentNode = walker.nextNode())) {
    const shadowRoot = (currentNode as Element).shadowRoot;
    if (shadowRoot) {
      // Only search inside open shadow roots
      iframes.push(...getAllIframesInRootNode(shadowRoot, true));
    }
    // For closed shadow roots, we can only detect them but can't search inside
  }

  return iframes;
}

export function findIframes(
  checkShadowRoot: boolean = false
): HTMLIFrameElement[] {
  return getAllIframesInRootNode(document, checkShadowRoot);
}

export function removeIframes(iframes: HTMLIFrameElement[]): string[] {
  iframes.forEach((iframe) => iframe.remove());
  return iframes.map((iframe) => iframe.getAttribute("src")!).filter(Boolean);
}

export function findAndRemoveIframes(
  checkShadowRoot: boolean = false
): string[] {
  const iframes = findIframes(checkShadowRoot);
  const removedIframes = removeIframes(iframes);
  return removedIframes;
}
