import {
  clearGlobalCursor,
  hideGlobalCursor,
  initializeGlobalCursor,
  moveGlobalCursor,
  showGlobalCursor,
  simulateGlobalCursorClick,
} from "./globalCursor";
import { findAndRemoveIframes, findIframes } from "./findAndRemoveIframes";
import { initSelectsOnPage } from "./initSelect";

type BoundingBox = {
  x: number;
  y: number;
  width: number;
  height: number;
};

export default defineContentScript({
  matches: ["<all_urls>"],
  runAt: "document_end",
  matchAboutBlank: true,
  allFrames: false,
  matchOriginAsFallback: true,
  main: () => {
    setTimeout(function () {
      const isArc =
        getComputedStyle(document.documentElement).getPropertyValue(
          "--arc-palette-background"
        ) !== "";

      if (isArc) {
        chrome.runtime.sendMessage({ type: "browserIsArc" });
      }
    }, 1000);

    chrome.runtime.onMessage.addListener((request, _sender, sendResponse) => {
      if (request.action === "simulateClick") {
        const { box } = request as { box: BoundingBox | null };
        if (box === null) {
          clearGlobalCursor();
          sendResponse({ success: true });
          return false;
        }

        (async () => {
          try {
            let left = box.x + box.width / 2;
            let top = box.y + box.height / 2;

            if (box.width > 30) {
              // If the input is wide enough, click near the left of it. This is visually
              // what makes sense (and what we do under the hood) when clicking on inputs.
              left = box.x + 30;
            }

            await moveGlobalCursor(left, top);
            await simulateGlobalCursorClick();
            sendResponse({ success: true });
          } catch (error) {
            sendResponse({ success: false, error: String(error) });
          }
        })();

        return true; // Indicates that we will send a response asynchronously
      }

      if (request.action === "simulateClickAt") {
        (async () => {
          const { x, y } = request as { x: number; y: number };
          await moveGlobalCursor(x, y);
          await simulateGlobalCursorClick();
          sendResponse({ success: true });
        })();

        return true; // Indicates that we will send a response asynchronously
      }

      if (request.action === "clearGlobalCursor") {
        clearGlobalCursor();
        sendResponse({ success: true });
        return false;
      }

      if (request.action === "hideGlobalCursor") {
        hideGlobalCursor();
        sendResponse({ success: true });
        return false;
      }

      if (request.action === "showGlobalCursor") {
        showGlobalCursor();
        sendResponse({ success: true });
        return false;
      }

      if (request.action === "getExtensionIframes") {
        const urls = findIframes(/*checkShadowRoot=*/ true);
        sendResponse({ urls });
        return false;
      }

      if (request.action === "removeExtensionIframes") {
        const urls = findAndRemoveIframes();
        sendResponse({ foundIframes: urls.length > 0, urls });
        return false;
      }

      if (request.action === "makeSelectClickable") {
        initSelectsOnPage();
        sendResponse({ success: true });
        return false;
      }

      return false; // Indicates that we won't use sendResponse for other actions
    });

    initializeGlobalCursor();

    function initDebugButton() {
      const button = new DOMParser().parseFromString(
        '<button id="openSidePanel" style="display: none;">Click to open side panel</button>',
        "text/html"
      ).body.firstElementChild;
      if (button?.tagName !== "BUTTON") {
        return;
      }
      button.addEventListener("click", function () {
        chrome.runtime.sendMessage({ type: "open_side_panel" });
        (button as HTMLElement).style.display = "none";
      });
      document.body.append(button);
    }

    initDebugButton();
    // initSelectsOnPage();
  },
});
